@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&family=Rubik:ital,wght@0,400;0,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
}

.container {
  width: 90vw;
  margin: 2vw 5vw;
  display: flex;
  justify-content: space-between;
}

#header {
  width: 100%;
  height: 42vw;
  background-color: white;
  background-image: url(/gyistvan-home/static/media/header-bg.91ab5090.png);
  background-repeat: no-repeat;
  background-size: 100% 42vw;
}

.navbar {
  display: flex;
  font-family: "Rubik Mono One", sans-serif;
  justify-content: space-between;
  width: 90vw;
  margin: 0vw 5vw;
}

.logo {
  text-transform: uppercase;
  color: white;
  padding: 2vw;
}

.menu {
  display: flex;
  justify-content: space-between;
  color: white;
  width: 30vw;
  padding: 2vw;
}

.logo-carousel {
  padding-top: 3.2vw;
  width: 22vw;
  display: flex;
  flex-direction: column;
}

.logo-carousel img:first-of-type {
  margin-bottom: -1.5vw;
}

.mainCarousel {
  width: 53vw;
  height: 30vw;
  background: grey;
}

.left {
  width: 30%;
  padding-right: 3%;
}

.left p {
  margin-top: 1vw;
  font-family: "Rubik";
  font-weight: 900;
}

.right {
  width: 70%;
  padding-left: 5%;
}

.right img {
  height: 20vw;
  object-fit: cover;
}

